body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.acd-joystick {
  position: absolute;
  z-index: 1;
  margin-left: 40px;
  margin-top: 32px; }

.acd-joystick__origin {
  -webkit-transition: -webkit-transform 0.1s linear;
  transition: -webkit-transform 0.1s linear;
  transition: transform 0.1s linear;
  transition: transform 0.1s linear, -webkit-transform 0.1s linear;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform-style: preserve-3D;
          transform-style: preserve-3D; }

.acd-joystick__stalk {
  height: 30px;
  width: 10px;
  display: block;
  background: #606060;
  border-left: 1px solid #505050;
  border-right: 1px solid #505050;
  left: 10px;
  border-radius: 2px;
  position: relative;
  box-shadow: 5px -4px 5px 0px rgba(0, 0, 0, 0.2); }

.acd-joystick:after {
  width: 30px;
  height: 30px;
  position: absolute;
  border-radius: 15px;
  background: black;
  content: '';
  z-index: -1;
  top: 45px;
  left: 0px;
  -webkit-transform: rotateX(-70deg);
          transform: rotateX(-70deg); }

.acd-joystick__ball {
  height: 30px;
  width: 30px;
  border-radius: 15px;
  display: block;
  background: -webkit-radial-gradient(#E80000, #A80000);
  background: radial-gradient(#E80000, #A80000);
  z-index: 1;
  top: 3px;
  position: relative;
  box-shadow: 5px -4px 5px 0px rgba(0, 0, 0, 0.2); }

.acd-joystick--UP {
  -webkit-transform: rotateX(20deg);
          transform: rotateX(20deg); }
  .acd-joystick--UP .acd-joystick__ball {
    box-shadow: 5px -6px 5px 0px rgba(0, 0, 0, 0.2); }
  .acd-joystick--UP .acd-joystick__stalk {
    box-shadow: 5px -2px 5px 0px rgba(0, 0, 0, 0.2), 5px -28px 5px 0px rgba(0, 0, 0, 0.2); }

.acd-joystick--DOWN {
  -webkit-transform: rotateX(-20deg);
          transform: rotateX(-20deg); }

.acd-joystick--LEFT {
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg); }
  .acd-joystick--LEFT .acd-joystick__stalk, .acd-joystick--LEFT .acd-joystick__ball {
    box-shadow: -5px -4px 5px 0px rgba(0, 0, 0, 0.2); }

.acd-joystick--RIGHT {
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg); }

.acd-button {
  position: absolute;
  overflow: hidden;
  margin: 0 auto;
  width: 40px;
  top: 40%;
  display: block;
  height: 50px;
  border-radius: 25px;
  background-color: rgba(160, 160, 160, 0.5);
  box-shadow: 2px -8px 10px 0px rgba(0, 0, 0, 0.2); }
  .acd-button:before {
    position: absolute;
    left: 0;
    top: 0px;
    width: 40px;
    height: 40px;
    border-radius: 48px;
    background-color: red;
    content: ''; }
  .acd-button:nth-child(1) {
    left: 330px;
    background-color: #c20000; }
    .acd-button:nth-child(1):before {
      background: red; }
  .acd-button:nth-child(2) {
    left: 380px;
    background-color: #004300; }
    .acd-button:nth-child(2):before {
      background: green; }
  .acd-button:nth-child(3) {
    left: 430px;
    background-color: #0000c2; }
    .acd-button:nth-child(3):before {
      background: blue; }
  .acd-button--DOWN:before {
    height: 45px; }

.acd-invader {
  display: inline-block;
  position: absolute; }
  .acd-invader .acd-invader-row {
    display: -webkit-flex;
    display: flex;
    height: 5px; }
    .acd-invader .acd-invader-row span {
      width: 5px;
      height: 5px;
      display: inline-block; }
      .acd-invader .acd-invader-row span.acd-invader-square--blue {
        background: #00EDFC; }
      .acd-invader .acd-invader-row span.acd-invader-square--orange {
        background: #FC8900; }
      .acd-invader .acd-invader-row span.acd-invader-square--pink {
        background: #FF33F6; }
      .acd-invader .acd-invader-row span.acd-invader-square--yellow {
        background: #ccff00; }
      .acd-invader .acd-invader-row span.acd-invader-square--green {
        background: #00FF00; }

.acd-cabinet {
  margin: 0 auto;
  width: 500px; }

.acd-window {
  border: 5px solid #383838;
  border-top: none;
  padding-top: 20px;
  width: 100%;
  height: 350px;
  background: #787899;
  box-shadow: inset 0px 11px 7px 0px rgba(0, 0, 0, 0.2); }

.acd-screen {
  border: 5px solid #383838;
  width: 90%;
  height: 85%;
  margin: 0px auto;
  background: #303030;
  position: relative;
  overflow: hidden;
  border-radius: 80px 80px 80px 80px / 20px 20px 20px 20px; }
  .acd-screen:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    opacity: 1;
    background: -webkit-linear-gradient(37deg, rgba(255, 255, 255, 0) 50%, rgba(247, 248, 240, 0.025) 50%, rgba(250, 245, 252, 0.08));
    background: linear-gradient(53deg, rgba(255, 255, 255, 0) 50%, rgba(247, 248, 240, 0.025) 50%, rgba(250, 245, 252, 0.08)); }

.acd-controls {
  width: 100%;
  height: 250px;
  top: -84px;
  position: relative;
  border: 5px solid #383838;
  border-top: none;
  -webkit-transform: rotateX(70deg);
          transform: rotateX(70deg);
  background: #787899;
  box-shadow: inset 0px 40px 40px 0px rgba(0, 0, 0, 0.2); }

.acd-top {
  width: 105%;
  height: 100px;
  left: -3%;
  position: relative;
  border: 5px solid #383838;
  background: #787899;
  text-align: center;
  overflow: hidden;
  color: orange; }
  .acd-top h1 {
    line-height: 100px;
    font-family: 'Faster One', cursive;
    font-size: 72px;
    font-weight: 200;
    margin: 0;
    background-image: -webkit-linear-gradient(right, red, yellow);
    background-image: linear-gradient(-90deg, red, yellow);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .acd-top .acd-invader:nth-child(2) {
    left: 4%;
    top: 18%; }
  .acd-top .acd-invader:nth-child(3) {
    left: 30%;
    top: 78%; }
  .acd-top .acd-invader:nth-child(4) {
    left: 85%;
    top: 50%; }
  .acd-top .acd-invader:nth-child(5) {
    left: 60%;
    top: -12%; }

.acd-front {
  width: 100%;
  height: 100vh;
  top: -168px;
  position: relative;
  border: 5px solid #383838;
  border-top-width: 3px;
  background: #787899; }

html {
  overflow: hidden;
  background-image: url(/arcade/assets/brickwall.jpeg);
  background-size: 100%;
}

.App {
  width: 100%;
}

